.form-container {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 2;
  display: flex;
  align-items: center;
  justify-content: center;

  .background-container {
    overflow-y: scroll;

    background-color: white;
    width: 30vw;
    height: 50vh;
    border-radius: 10px;
    padding: 2rem;
    margin: 0;
    form {
      display: flex;
      justify-content: start;
      flex-direction: column;
      align-items: center;
      height: 100%;

      label {
        width: 80%;
      }
      input {
        width: 80%;
        margin-bottom: 1rem;
      }

      .button-area {
        display: grid;
        justify-items: center;
        grid-template-columns: 1fr 1fr;
        width: 100%;
        button {
          background-color: red;
          width: 100px;
          height: 30px;
          border: none;
          border-radius: 5px;
          color: white;
          cursor: pointer;

          &:hover {
            background-color: rgb(117, 7, 7);
          }
        }

        .submit {
          background-color: rgb(19, 143, 19);
          width: 100px;
          height: 30px;
          border: none;
          border-radius: 5px;
          color: white;
          cursor: pointer;
          font-family: "Roboto Mono", monospace;

          &:hover {
            background-color: rgb(13, 103, 13);
          }
        }
      }
      .tags-section{
        width: 100%;
        display: flex;
        flex-direction: column;
        .tag-input{
          display: grid;
          grid-template-columns: 10fr 1fr;
          justify-items: center;
          
        }

        .tag-container{
          display: flex;
          flex-wrap: wrap;
          .tag{
            background-color: #DD4D1E;
            border-radius: 8px;
            color: white;
            display: flex;
            align-items: center;
            font-size: 14px;
            width: fit-content;
            margin: .1rem .1rem;
            padding: 0 5px;
            svg{
              width: 14px;
              height: 14px;
              fill: white;
            }
          }
        }

        justify-content: center;
        .tag-input{
          
          svg {
            width: 20px;
            height: 20px;
          }
        }
        
      }
    }
  }
}
