.course-container{
    display: flex;
    flex-direction: column;
    height: 100%;
    .course-detail-header{
        display: flex;
        flex-direction: column;
        margin: 1rem 0rem;
        h1{
            margin-bottom: 0;
        }
        .course-rating{
            display: flex;
            align-items: center;
            font-weight: 700    ;
            
            span{
                padding: 0 .2rem 0 0
                
            }
            svg{
                fill:#E9E245;
            }
    
        }
    }


    .course-grid{
        display: grid;
        grid-template-columns: 3.5fr 2fr;
        grid-template-rows: auto;
        gap: 1rem;

        .course-image{
            background-image: linear-gradient(#0073FC, #556AFE, #8266FF);
            width: 100%;
            height: 45vh;
            border-radius: 5px;
            svg{
                fill:white;
                width:45px;
                height: 45px;
                margin: .2rem 0rem 0rem 1rem;
                cursor: pointer;
            }
        }

        .review-section{
            display: flex;
            flex-direction: column;
            align-items: center;

            .no-review-found{
                display: flex;
                flex-direction: column;
                align-items: center;

                h4{
                    margin: 1rem 0rem;
                }
            }
            
        }
    }

    .comment-section{
        margin: 2rem 0;
        
        h4{
            margin:1rem 0 ;
        }
    }
}